.login-page{
    min-height: 100vh;
    background: #eee;
    padding-top: 100px;
}
.login-form{
    max-width: 500px;
    margin: auto;
    background: #fff;
    padding: 30px;
    box-shadow: 0px 0px 15px rgba(0,0,0,.2);
    border-radius: 5px;
}
.login-form h1{
    text-align: center;
    display: block;
    width: 100%;
    font-size: 22px;
    margin-bottom: 20px;
}
.post-content img {
    max-width: 100%
}
.admin-row{
    padding-top: 0px;
    padding-left: 20px;
    margin-top: 20px;
    display: flex;
    align-items: flex-start;
    flex-direction: row;
    justify-content: space-evenly;   
    width: 100%;
    height: 100%;
}
.admin-col {
    display: flex;
    align-items: flex-start;
    justify-content: center;
}
.admin-tale {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 18vw;
    height: 18vw;
}
.admin-title {
    font-size: 20px;
    font-style: normal;
    font-weight: bold;
    margin-left: 10px;
    margin-top: 10px;
}

.login-page-row {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-content: space-between;
    width: 100%;
}
.login-page-text {
    width: 200px;
    text-align: right;
    margin-right: 15px;
}
.login-page-button {
    margin-left: 15px;
}
.admin-user-menu{
    width: 25%;
    height: 100%;
    min-width: 120px;
    margin-top: 20px;
    margin-right: 20px;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    flex-direction: column;
    padding-top: 50px;
    text-align: center;
}
.admin-user-menu .text-1 {
    font-size: 20px;
    padding-top: 8px;
    padding-bottom: 8px;
}
.admin-user-menu .text-3 {
    padding-top: 8px;
    padding-bottom: 8px;
    font-size: 15px;
    font-style: italic;
    font-weight: lighter;
}
.admin-user-menu .text-2 {
    padding-top: 8px;
    font-size: 16px;
    font-style: normal;
    font-weight: normal;
}
.admin-user-menu .avatar {
    width: 100px;
    height: 100px;
}
.dashboard-counter {
    font-size: 50px;
    color:royalblue;
    font-weight: normal;
}
@media only screen and (max-width: 600px) {
    .admin-user-menu .avatar {
        width: 50px;
        height: 50px;
    }
    .dashboard-counter {
        font-size: 25px;
        font-weight: bold;
    }
  }
  @media only screen and (min-width: 1400px) {
    .admin-tale {
        width: 15vw;
        height: 15vw;
    }
  }
.dashboard-main-row{
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    height: 100%;
}

.p100 {
    height: 85vh;
}
.admin-divider {
    width: 100%;
    display: inline;
}