.outline {
    text-shadow: 0 0 5px #000, 0 0 7px #0000;
}

.blogPost {
    display: flex;
    justify-content: center;
}
.content{
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-content: center;
    padding-top: 20px;
    align-items: center;
}
.divider {
    display: inline;
}
@media only screen and (max-width: 1000px) {
    .blogPost {
        flex-direction: column;
    }
  }
.date-text {
    font-size: 12px;
    font-weight: lighter;
}
.date-text-row {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
.comment-section {
    background: #FFFFF5;
    width: 78.4%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-top: 10px;

    margin-bottom: 10px;
    
}
.comment-section .comment {
    width: 100%;
    overflow-y:auto;
    height: 320px;
    align-items: flex-start;
}
.comment-section .comment-column {
    display: flex;
    flex-direction: column;
    padding-left: 10px;
}
.comment-section .comment-row {
    margin-top: 10px;
    margin-left: 30px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding-top: 10px;
    align-items:flex-start;
    width: 100%;
    background: #FAFAFA;
    margin-bottom: 10px;
} 
.comment-section .comment-row .start {
    justify-content: flex-start;
    flex-direction: row;
    display: inline-flex;
}
.comment-section .comment-row .end {
    justify-content: flex-end;
    flex-direction: row;
    display: inline-flex;
    padding-right: 10px;
}
.comment-section .comment-row .comment-date {
    font-size: 12px;
    font-weight: lighter;
    padding-right: 10px;
}
.comment-section .comment-row .user-icon {
    padding-left: 10px;
    padding-top: 10px;
    width: 50px;
    height: 50px;
}
.comment-section .comment-row .end .comment-icon {
    color: black;
}
.comment-section .comment-row .end .empty-icon {
    height: 24px;
    width: 24px;
}
.comment-section .comment-row .end .comment-icon:hover {
    color: red;
}
.comment-section .comment-builder {
    padding-bottom: 10px;
}
.blog-item {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}
.blog-item .blog-item-icon {
    width: 80px;
    height: 80px;
}
.blog-login-button {
    background: rgba(0, 0, 0, 0);
    border-radius: 5px;
    border: 2px solid #FFF;
    position: absolute;
    color: #FFFF;
    top: 5px;
    right: 5px;
}
.blog-register-button {
    background: rgba(0, 0, 0, 0);
    border-radius: 5px;
    border: 2px solid #FFF;
    position: absolute;
    color: #FFFF;
    top: 5px;
    right: 75px;
}